<script>
	import Map from './Map.svelte';
	import Panel from './Panel.svelte';
	import Select from './Select.svelte';
	import GridChart from './GridChart.svelte';
	import { getData, getPlace, getBoundary, procData, getSum } from './utils.js';
	
	const dataurl = 'https://raw.githubusercontent.com/bothness/geo-data/main/csv/towns-data-2020.csv';
	const mapstyle = 'https://bothness.github.io/ons-basemaps/data/style-omt.json';
	const regions = {
		'E12000001': 'North East',
		'E12000002': 'North West',
		'E12000003': 'Yorkshire and The Humber',
		'E12000004': 'East Midlands',
		'E12000005': 'West Midlands',
		'E12000006': 'East of England',
		'E12000007': 'London',
		'E12000008': 'South East',
		'E12000009': 'South West',
		'W92000001': 'Wales'
	};
	let map = null;
	let towns;
	let selected = null;
	let data;
	let sum;
	
	function doSelect(event) {
		let code = event.detail.code;
		if (code == null) {
			let id = 'boundary';
			if (map.getLayer(id)) {
				map.removeLayer(id);
				map.removeLayer(id + '-fill');
				map.removeSource(id);
			}
			data = null;
			sum = null;
			map.fitBounds([[-5.816, 49.864], [1.863, 55.872]]);
		} else {
			data = null;
			sum = null;
			getPlace(code).then(res => {
				data = res.sort((a, b) => a.age.localeCompare(b.age)).sort((a, b) => a.sex.localeCompare(b.sex));
				if (data[1]) {
					sum = getSum(data);
					data = procData(data, sum);
				}
			});
		
			// Get geographic boundary of selected place
			let id = 'boundary';
			let boundary;
			getBoundary(code)
			.then(result => {
				// Remove previous boundary from the map
				if (map.getLayer(id)) {
					map.removeLayer(id);
					map.removeLayer(id + '-fill');
					map.removeSource(id);
				}
			
				// Add new boundary to the map
				map.addSource(id, {
					'type': 'geojson',
					'data': result.geometry
				});
				map.addLayer({
					'id': id + '-fill',
					'type': 'fill',
					'source': id,
					'layout': {},
					'paint': {
						'fill-color': 'rgba(26, 94, 150, 0.1)'
					}
				}, 'boundary_country');
				map.addLayer({
					'id': id,
					'type': 'line',
					'source': id,
					'layout': {},
					'paint': {
						'line-color': 'rgb(26, 94, 150)',
						'line-width': 2
					}
				}, 'boundary_country');
				
				// Fit map to boundary
				map.fitBounds(result.bounds, { padding: 20 });
			});
		}
	}
	
	getData(dataurl).then(res => {
		res.forEach(d => {
			d.name = d.name.replace(' BUASD', '');
			d.name = d.name.replace(' BUA', '');
		})
		towns = res.sort((a, b) => a.name.localeCompare(b.name));
	});
</script>

<style>
	:global(body) {
		padding: 0;
		margin: 0;
	}
	main {
		width: calc(100% - 24px);
		padding: 12px;
		margin: 0;
	}
	.muted {
		color: #777;
	}
</style>

<Panel>
	<main>
		<h1>English &amp; Welsh Towns</h1>
		{#if towns}
		<Select options={towns} bind:selected={selected} search={true} on:select={doSelect} />
		{/if}
		{#if sum}
		<p>Total population {sum.toLocaleString()}</p>
		{/if}
		{#if data}
		<GridChart {data} />
		{/if}
		<p><small class="muted">Data aggregated by COGS from ONS <a href="https://staging.gss-data.org.uk/cube/explore?uri=http%3A%2F%2Fgss-data.org.uk%2Fdata%2Fgss_data%2Ftowns-high-streets%2Fons-population-estimates-by-output-areas-electoral-health-and-other-geographies-england-and-wales-catalog-entry" target="_blank">2018 mid-year population estimates</a> for England and Wales.</small></p>
	</main>
</Panel>

<Map style={mapstyle} bind:map={map} />

