<script>
	import { createEventDispatcher } from 'svelte';
	
	const dispatch = createEventDispatcher();
	
	export let options;
	export let selected;
	export let search = false;
	
	let expanded = false;
	let filter = '';
	let el;
	let input;
	
	$: regex = filter ? new RegExp(filter, 'i') : null;
	$: filtered = regex ? options.filter(option => regex.test(option.name)) : options;
	
	function toggle(ev) {
		ev.stopPropagation();
		filter = '';
		expanded = !expanded;
	}
	
	$: if (input && expanded) {
		input.focus();
	}
	
	function select(option) {
		dispatch('select', {
			code: option.code
		});
		selected = option;
		expanded = false;
	}
	
	function unSelect(ev) {
		ev.stopPropagation();
		selected = null;
		dispatch('select', {
			code: null
		});
	}
	
	$: document.onclick = function(e){
		if(e.target !== el){
			expanded = false;
		}
	};
</script>

<style>
	a {
		text-decoration: none;
		display: block;
		padding: 0;
		border: 2px solid #206095 !important;
	}
	a span {
		display: inline-block;
		padding: 10px 5px;
	}
	#dropdown ul {
		list-style: none;
		margin: 0;
		padding: 0;
		max-height: 200px;
		overflow-y: auto;
		overflow-x: hidden;
	}
	#dropdown li {
		line-height: 1em;
		padding: 6px 0;
	}
	#dropdown li:hover {
		color: #fff;
		background-color: #206095;
		font-weight: 500;
	}
	/* normalize the input elements, make them look like everything else */
	#select input {
		background: transparent;
		font-family: inherit;
		font-size: inherit;
		color: inherit;
		font-weight: inherit;
		line-height: inherit;
		display: inline-block;
		padding: 10px 5px;
		margin: 0;
		width: 100%;
		background-color: #fff;
		border: 2px solid #206095 !important;
		border-radius: 0px;
		-webkit-appearance: none;
		-moz-appearance: none;
	}
	#select input:focus {
		outline: none;
	}
	/* custom field (drop-down, text element) styling  */
	#select {
		display: block;
		width: 100%;
		position: relative;
	}
	.active {
		z-index: 1000;
	}
	/* the toggle is the visible part in the form */
	#toggle,
	#select input {
		line-height: inherit;
		color: #206095;
		font-weight: 500;
		cursor: pointer;
	}
	/* drop-down list / text element */
	#dropdown {
		width: 100%;
		position: absolute;
		background: #eee;
		top: 0;
		left: 0;
		opacity: 1;
	}
	.selected {
		color: #fff !important;
		background-color: #206095;
	}
	.button {
		color: #fff;
		background-color: #206095;
		background-repeat: no-repeat;
		background-position: center;
		display: inline-block;
		float: right;
	}
	.down {
		background-image: url("https://onsvisual.github.io/maptemplates/simplemap/images/icons--chevron-down.svg");
		width: 30px;
	}
	.close {
		background-image: url("https://onsvisual.github.io/maptemplates/simplemap/images/x_close_slate.svg");
		width: 8px;
		margin-right: 10px;
	}
</style>

<div id="select" class:active={expanded}>
	{#if selected}
	<a id="toggle" class="selected" on:click={toggle}>
		<span>{selected.name}</span>
		<span class="button close" on:click={unSelect}>&nbsp;</span>
	</a>
	{:else}
	<a id="toggle" on:click={toggle}>
		<span>Select a town</span>
		<span class="button down">&nbsp;</span>
	</a>
	{/if}
	{#if expanded}
	<div id="dropdown" bind:this={el}>
		{#if search == true}
		<input type="text" placeholder="" bind:value={filter} autocomplete="false" bind:this={input} />
		{/if}
		<ul>
			{#if filtered[0]}
			{#each filtered as option}
			<li on:click="{() => select(option)}">{option.name}</li>
			{/each}
			{:else}
			<li>No results</li>
			{/if}
		</ul>
	</div>
	{/if}
</div>