<script>
	export let data;
	
	let sum = 0;
	let male = 0;
	let female = 0;
	
	data.forEach(d => {
		let val = d.value;
		sum += val;
		if (d.sex == "Female") {
			female += val;
		} else {
			male += val;
		}
	});
</script>

<style>
	#grid {
		width: 100%;
		height: 250px;
	}
	.row {
		display: block;
	}
	.f {
		background-color: #206095;
		display: inline-block;
		outline: 1px solid white;
		color: white;
		font-size: 0.9em;
		padding: 2px;
		float: left;
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
	}
	.m {
		background-color: #27A0CC;
		display: inline-block;
		outline: 1px solid white;
		font-size: 0.9em;
		padding: 2px;
		float: right;
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
	}
	.r {
		float: right;
		color: rgba(255, 255, 255, 0.5);
	}
</style>

{#if data.length == 10}
<div>
	<span style="color: #206095">Female {((female / sum) * 100).toFixed(1)}%</span>
	<span style="color: #27A0CC; float: right">Male {((male / sum) * 100).toFixed(1)}%</span>
</div>

<div id="grid">
	<div class="row" style="height: {data[0].height}%">
		<div class="f" style="top: 0; height: 100%; width: {data[0].width}%">0-15<span class="r">{data[0].value.toLocaleString()}</span></div>
		<div class="m" style="top: 0; height: 100%; width: {data[5].width}%"><span class="r">{data[5].value.toLocaleString()}</span></div>
	</div>
	<div class="row" style="height: {data[1].height}%">
		<div class="f" style="top: 0; height: 100%; width: {data[1].width}%">16-24<span class="r">{data[1].value.toLocaleString()}</span></div>
		<div class="m" style="top: 0; height: 100%; width: {data[6].width}%"><span class="r">{data[6].value.toLocaleString()}</span></div>
	</div>
	<div class="row" style="height: {data[2].height}%">
		<div class="f" style="top: 0; height: 100%; width: {data[2].width}%">25-49<span class="r">{data[2].value.toLocaleString()}</span></div>
		<div class="m" style="top: 0; height: 100%; width: {data[7].width}%"><span class="r">{data[7].value.toLocaleString()}</span></div>
	</div>
	<div class="row" style="height: {data[3].height}%">
		<div class="f" style="top: 0; height: 100%; width: {data[3].width}%">50-64<span class="r">{data[3].value.toLocaleString()}</span></div>
		<div class="m" style="top: 0; height: 100%; width: {data[8].width}%"><span class="r">{data[8].value.toLocaleString()}</span></div>
	</div>
	<div class="row" style="height: {data[4].height}%">
		<div class="f" style="top: 0; height: 100%; width: {data[4].width}%">65+<span class="r">{data[4].value.toLocaleString()}</span></div>
		<div class="m" style="top: 0; height: 100%; width: {data[9].width}%"><span class="r">{data[9].value.toLocaleString()}</span></div>
	</div>
</div>
{/if}